@import "styles/variables.scss";

.personalData > input,
.personalData > div {
    margin-bottom: 24px;
}

.personalData > p {
    transform: translateY(-16px);
    font-size: 12px;
    line-height: 140%;
    letter-spacing: -0.02em;
    font-feature-settings: 'zero' on;
}

.inputMask {
    border: none;
    background-color: $beige;
    padding-bottom: 8px;
    width: 100%;
    border-bottom: 1px solid rgba(27, 37, 48, 0.5);
    margin-bottom: 0;
    letter-spacing: -0.02em;
    font-feature-settings: 'zero' on;
    line-height: 125%;
    box-sizing: border-box;
    padding: 0 0 8px;
}

.inputMask:focus-visible {
    outline: none;
    border-bottom: 1px solid rgba(27, 37, 48, 1);
}

.inputMask::placeholder {
    color: $darkBlue;
    opacity: 0.4;
}

.inputError {
    color: $redColor;
    border-bottom: 1px solid #EF3A3A;
}

.errorText {
    transform: translateY(5px);
    font-size: 12px;
    line-height: 140%;
    letter-spacing: -0.02em;
    font-feature-settings: "zero" on;
    margin-top: 4px;
    color: #EF3A3A;
}

@media #{$mobile} {
    .inputMask {
        font-size: 16px;
        line-height: 100%;
    }
    .personalData > p {
        transform: translateY(-20px);
    }
}