@import "styles/variables.scss";

.radioGroup label {
    max-width: fit-content;
}

.installment {
    max-width: 250px;
    line-height: 120%;
    letter-spacing: -0.02em;
}

.sidebarText {
    max-width: 295px;
    line-height: 125%;
    letter-spacing: -0.02em;
    font-feature-settings: 'zero' on;
    padding-bottom: 32px;
    border-bottom: 1px solid rgba(35, 35, 35, 0.3);
    margin-bottom: 32px;
}

.lastSidebarText {
    border-bottom: none;
    margin-bottom: 0;
    padding-bottom: 24px;
}

.sidebarTitle {
    margin-bottom: 24px;
    line-height: 100%;
    text-transform: uppercase;
}

.sidebarUl {
    margin: 0;
    line-height: 125%;
    letter-spacing: -0.02em;
    font-feature-settings: 'zero' on;
    padding-left: 25px;
}

@media #{$tablet} {
    p.sidebarText {
        font-size: 12px!important;
        padding-bottom: 24px;
        margin-bottom: 24px;
        max-width: 100%;
    }
    p.lastSidebarText {
        padding-bottom: 16px;
        margin-bottom: 0;
    }
    .sidebarUl {
        padding-left: 18px;
    }
}