@import "styles/variables.scss";

.products {
    position: relative;
    max-height: 624px;
}

.products > .productItem {
    border-bottom: 1px solid rgba(35, 35, 35, 0.3);
    padding: 16px 0;
}

.products > .productItem:first-child {
    border-top: 1px solid rgba(35, 35, 35, 0.3);
}

.productItem {
    display: flex;
    justify-content: space-between;
    position: relative;
}

.productInfo {
    display: flex;
}

.productInfo > img {
    background-image: url('/static/img/miniLoader.svg');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: 50% 50%;
}

.productInfo > div {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-left: 24px;
}

.productInfo > div a {
    text-decoration: none;
}

.productInfo > div a > p:first-child {
    margin-bottom: 8px;
    font-size: 28px;
    font-family: "Suisse Int'l", "20Kopeek-Book", sans-serif;
    width: 324px;
}

.productInfo > div p {
    letter-spacing: -0.02em;
}

.productInfo > div > p {
    font-size: 28px;
    font-family: "Suisse Int'l", "20Kopeek-Book", sans-serif;
}

.outOfStock {
    color: #E41616;
}

.counterAndSizeBlock {
    position: absolute;
    left: 50%;
}

.counterAndSizeBlock > div:last-child {
    margin-top: 12px;
}

.buttonGroup > img {
    cursor: pointer;
}

.buttonGroup > img:first-child {
    margin-right: 8px;
}

.sizeList > p {
    margin-bottom: 8px;
}

@media #{$desc} {
    .productInfo > div a > p:first-child {
        width: 190px;
    }
}

@media #{$more-tablet} {
    .productInfo > img {
        width: auto;
    }
}

@media #{$tablet} {
    .products {
        max-height: none;
        padding-bottom: 85px;
        overflow-x: hidden;
    }
    .productItem {
        display: grid;
        grid-template-rows: repeat(2, 1fr);
        grid-template-columns: repeat(2, 1fr);
        width: fit-content;
        margin: auto;
    }
    .productInfo {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        grid-template-rows: 1fr 0.6fr;
        grid-column-start: 1;
        grid-column-end: 3;
        grid-row-start: 1;
        grid-row-end: 3;
        width: fit-content;
    }
    .productInfo > img {
        grid-column-start: 1;
        grid-column-end: 2;
        grid-row-start: 1;
        grid-row-end: 3;
        height: auto;
        width: 134px;
        align-items: end;
    }
    .productInfo > div {
        grid-column-start: 2;
        grid-column-end: 3;
        grid-row-start: 1;
        grid-row-end: 2;
        margin-left: 16px;
    }
    .productInfo > div a > p:first-child,
    .productInfo > div > div > p:nth-child(2) {
        font-size: 16px;
        line-height: 100%;
        text-transform: uppercase;
    }
    .productInfo > div a > p:first-child {
        margin-top: 24px;
        max-width: 218px;
        min-width: 218px;
    }
    .productInfo > div > p {
        font-size: 12px;
        line-height: 125%;
        letter-spacing: -0.02em;
        font-feature-settings: 'zero';
        margin-top: 8px;
    }
    .counterAndSizeBlock {
        display: flex;
        grid-column-start: 2;
        grid-column-end: 3;
        grid-row-start: 2;
        grid-row-end: 3;
        position: static;
        transform: translateX(-35px);
        position: absolute;
        bottom: -20px;
        left: auto;
    }
    .counterAndSizeBlock > div:last-child {
        margin-top: 0;
        transform: translateX(40px);
    }
    .buttonGroup {
        position: absolute;
        right: 0;
        top: 16px;
    }
}

@media #{$mobile} {
    .counterAndSizeBlock {
        bottom: 0;
    }
}

