@import "styles/variables.scss";

.title {
    margin-bottom: 16px;
    line-height: 100%;
    letter-spacing: -0.02em;
    font-feature-settings: 'zero' on;
}

.buttonGroup {
    display: flex;
}

.buttonGroup > button {
    width: 100%;
}

.buttonGroup > button:first-child {
    margin-right: 8px;
}

.radioGroup {
    margin-top: 24px;
}

@media #{$tablet} {
    .title {
        font-size: 16px;
    }
    .radioGroup {
        margin-top: 16px;
    }
}