@import "styles/variables.scss";

.counter > p {
    margin-bottom: 8px;
}

.count {
    display: flex;
}

.count > div,
.count > button {
    width: 40px;
    height: 40px;
    background-color: transparent;
    border: 1px solid rgba(35, 35, 35, 0.3);
    line-height: 40px;
    vertical-align: middle;
    text-align: center;
    box-sizing: border-box;
    text-transform: uppercase;
    cursor: pointer;
}

.count > div,
.count > button:last-child {
    border-left: none;
}

@media #{$tablet} {
    .count > div,
    .count > button {
        width: 32px;
        height: 32px;
        line-height: 32px;
    }
}