@import "styles/variables.scss";

.addressForm {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.addressForm > div:not(:last-child) {
    margin-bottom: 24px;
}

.addressForm > div:nth-child(2) {
    margin-right: 32px;
}

.addressForm > div:first-child,
.addressForm > div:last-child {
    grid-column-start: 1;
    grid-column-end: 3;
    width: 100%;
}

@media #{$tablet} {
    .addressForm > div:nth-child(2),
    .addressForm > div:nth-child(4) {
        margin-right: 24px;
    }
}