@import "styles/variables.scss";

.checkout {
    display: flex;
    justify-content: space-between;
    min-height: 460px;
}

.checkout > div {
    width: 40%;
}

.personalData {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.personalData > p,
.personalData > p > a {
    font-size: 12px;
    line-height: 140%;
    letter-spacing: -0.02em;
    font-feature-settings: 'zero' on;
}

.personalData > p > a {
    text-decoration: none;
    border-bottom: 1px solid $darkBlue;
}

.contentInfo {
    display: flex;
    justify-content: center;
    align-items: center;
    height: calc(100vh - 395px);
}

@media screen and (max-width: 1680px) {
    .content {
        margin-left: 140px;
        margin-right: 140px;
    }
}

@media #{$tablet} {
    .content {
        margin-left: 12px;
        margin-right: 12px;
    }
    .checkout {
        flex-direction: column;
    }
    .checkout > div { 
        width: 100%;
    }
    .checkout > div:last-child {
        margin-top: 40px;
    }
    .contentInfo {
        height: calc(100vh - 490px);
    }
}