@import "styles/variables.scss";

.checkoutInfo {
    position: sticky;
    top: 100px;
}

.info {
    border-top: 1px solid rgba(35, 35, 35, 0.3);
    padding-top: 16px;
}

.info > div {
    display: flex;
    justify-content: space-between;
}

.info > div > p {
    line-height: 125%;
    letter-spacing: -0.02em;
    font-feature-settings: 'zero' on;

}

.info > div:not(:first-child) {
    margin-top: 8px;
}

.info > div:last-child {
    margin-top: 16px;
    border-top: 1px solid rgba(35, 35, 35, 0.3);
    padding-top: 16px;
}

.info > div:last-child > p:first-child {
    text-transform: uppercase;
}

.promo {
    width: 100%;
    margin-top: 24px;
    position: relative;
}

.promoInput {
    width: 100%;
    height: 40px;
    border: 1px solid rgba(35, 35, 35, 0.3);
    border-radius: 2px;
    background-color: $beige;
    padding: 0 0 0 14px;
    box-sizing: border-box;
    font-size: 16px;
    line-height: 125%;
    letter-spacing: -0.02em;
    font-feature-settings: 'zero'
}

.promoButton {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 14px;
    background-color: $beige;
    font-size: 12px;
    cursor: pointer;
}

.promoButton img {
    display: block;
}

.errorText {
    color: $redColor;
    font-size: 12px;
    line-height: 140%;
    letter-spacing: -0.02em;
    font-feature-settings: 'zero' on;
    margin-top: 8px;
}

button.sendBtn {
    margin-top: 15px;
    width: 100%;
}

.policy {
    margin-top: 15px;
}

.policy > p {
    line-height: 20px;
    letter-spacing: -0.02em;
    font-size: 12px;
}

.policy > p > a {
    border-bottom: 1px solid $darkBlue;
    text-decoration: none;
    font-size: 12px;
}

@media #{$tablet} {
    .info > div:not(:first-child) {
        margin-top: 4px;
    }
    .info > div:last-child {
        margin-top: 16px;
    }
}