@import "styles/variables.scss";

.sizeList > p {
    margin-bottom: 8px;
}

.sizes {
    display: flex;
    justify-content: space-between;
    width: fit-content;
    flex-wrap: wrap;
    max-height: 42px;
    overflow: hidden;
}

.sizes > div:first-child a {
    margin-right: 4px;
    text-decoration: none;
}

.size {
    width: fit-content;
    min-width: 30px;
    height: 40px;
    padding: 0 5px;
    margin-bottom: 12px;
    text-align: center;
    line-height: 40px;
    vertical-align: middle;
    font-size: 16px;
    border: 1px solid rgba(35, 35, 35, 0.3);
    color: rgba(35, 35, 35, 0.3);
    cursor: pointer;
}

.sizes > .size:not(:last-child) {
    margin-right: 4px;
}

@media #{$tablet} {
    .sizes {
        align-items: flex-end;
        margin-bottom: 16px;
    }
    .size  {
        margin-bottom: 0;
    }
}