@import "styles/variables.scss";

.orderInfo {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.orderInfo > p {
    text-align: center;
}

.orderInfo > p:first-child {
    font-size: 46px;
    line-height: 110%;
    letter-spacing: -0.03em;
}

.orderInfo > :nth-child(2n) {
    margin-top: 24px;
}

@media #{$tablet} {
    .orderInfo > p:first-child {
        font-size: 32px;
        letter-spacing: -0.02em;
    }
    .orderInfo > :nth-child(2n) {
        margin-top: 16px;
    }
}