@import "styles/variables.scss";

.location {
    display: flex;
    justify-content: space-between;
}

.location > div {
    width: 100%;
}

.location > div:first-child {
    margin-right: 32px;
}

@media #{$tablet} {
    .location {
        flex-direction: column;
    }
    .location > div:first-child {
        margin-right: 0;
        margin-bottom: 24px;
    }
}